@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto&display=swap);
header a,nav,.btn-delete{display:flex;justify-content:center;align-items:center}section,.button-row,.container-column h2{display:flex;justify-content:center}h2{display:flex;align-items:center}html{scroll-behavior:smooth}body{margin:0;padding:0;top:0;left:0;font-family:'Open Sans','Roboto', sans-serif;background-color:#eee;color:#000}a{text-decoration:none;color:#ef5338}header{overflow:hidden;height:50px;background-color:#ef5338;color:white;position:fixed;top:0;width:100%;display:flex;flex-wrap:nowrap;justify-content:space-between;align-items:center}header a{color:#fff}header h1{font-size:1.2rem;font-weight:normal;margin-left:20px}header ul{display:flex;font-size:0.9rem}h2{font-size:0.9rem;font-weight:normal;margin:0;padding:0;background-color:#8AB7E5;flex-grow:1;padding-left:10px}nav{margin-top:50px;height:30px;font-size:0.8rem;background-color:#313e6f;color:#fff}ul{list-style:none;margin:0;padding:0}li{color:white;margin:10px;display:flex;flex-wrap:nowrap;justify-content:space-between}.box{background-color:white;margin:20px 0px;width:500px}.button-row{margin:10px}.button-row input{flex-grow:5;padding-left:10px;margin-right:10px;border:1px solid lightgrey}.btn{cursor:pointer;background-color:#313e6f;color:white;padding:10px 20px}.btn:hover{background-color:#ef5338}.btn-delete{background-color:#313e6f;height:25px;line-height:25px;padding:0 10px;font-size:0.8rem;cursor:pointer}.btn-delete:hover{background-color:#ef5338}.container-column{margin-top:5%;display:flex;flex-direction:column;background-color:white;padding:10px}.container-column h2{background-color:none;padding:4px}.container-column form{display:flex;flex-direction:column;margin-top:10px}.container-column form input{margin:5px 0;padding:5px 10px;font-size:0.8rem;min-width:200px;border:1px solid #8AB7E5}.alert{padding-top:5px;color:red;font-size:0.80rem}

